ul.scene2d {
  list-style: none;
  position: relative;
  overflow: hidden;
  margin-bottom: 0;
}

ul.scene2d>li {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 1;
  -webkit-transition: left 1s;
  -o-transition: left 1s;
  transition: left 1s;
  overflow: auto;
  padding-top: 80px;
  padding-bottom: 80px;
}

ul.scene2d>li.left {
  left: -100%;
}

ul.scene2d>li.right {
  left: 100%;
}


