.article-wrapper {
  position: relative;
  min-height: 100%;
}

.bg-article {
  opacity: 0.382;
  -webkit-filter: blur(.2em);
          filter: blur(.2em);
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  text-shadow: 0 0 1px blueviolet;
}

.ft-article {
  position: absolute;
  top: 0;
  left: 15px;
  right: 15px;
}

.article-printing:after {
  content: "|";
  -webkit-animation: 0.5s linear 0s infinite alternate both running bling;
          animation: 0.5s linear 0s infinite alternate both running bling;
}

.handle {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}


