*, *:before, *:after {
  margin: 0;
  padding: 0;
  border: none;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

/* for small OR large */

.for-small {
  display: block;
}

.for-large {
  display: none;
}

@media (min-width: 768px) {
  .for-small {
    display: none;
  }
  .for-large {
    display: block;
  }
}

/* commons */

@-webkit-keyframes bling {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes bling {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}


