.demonstration {
  position: fixed;
  z-index: 999999;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

.demonstration-text {
  background-color: darkblue;
  color: whitesmoke;
  text-shadow: 0 0 5px white;
  padding: 0.5em;
  border-radius: 0.255rem;
}

@-webkit-keyframes moving_right_to_left {
  from {
    opacity: 0;
    -webkit-transform: translateX(200px);
            transform: translateX(200px);
  }
  20% {
    opacity: 1;
    -webkit-transform: translateX(160px);
            transform: translateX(160px);
  }
  80% {
    opacity: 1;
    -webkit-transform: translateX(-160px);
            transform: translateX(-160px);
  }
  to {
    opacity: 0;
    -webkit-transform: translateX(-200px);
            transform: translateX(-200px);
  }
}

@keyframes moving_right_to_left {
  from {
    opacity: 0;
    -webkit-transform: translateX(200px);
            transform: translateX(200px);
  }
  20% {
    opacity: 1;
    -webkit-transform: translateX(160px);
            transform: translateX(160px);
  }
  80% {
    opacity: 1;
    -webkit-transform: translateX(-160px);
            transform: translateX(-160px);
  }
  to {
    opacity: 0;
    -webkit-transform: translateX(-200px);
            transform: translateX(-200px);
  }
}

.hand-img {
  position: relative;
  top: -100px;
  -webkit-transform-origin: 50% 50% 0;
      -ms-transform-origin: 50% 50% 0;
          transform-origin: 50% 50% 0;
  -webkit-animation: 2s linear 0s infinite normal both running moving_right_to_left;
          animation: 2s linear 0s infinite normal both running moving_right_to_left;
}

.next-img {
  position: relative;
  top: -200px;
  left: -20px;
}

.static-bottom {
  position: relative;
  top: -20px;
}

@media (min-width: 768px) {
  .hand-img {
    -webkit-animation: 1s linear 0s infinite alternate both running bling;
            animation: 1s linear 0s infinite alternate both running bling;
  }
}


