.ani {
  opacity: 1;
  position: relative;
  left: 0;
  top: 0;
  -webkit-transition: all 1s;
  -o-transition: all 1s;
  transition: all 1s;
}

.ani.fade-in-right {
  opacity: 0;
  left: -2em;
}

.ani.fade-in-left {
  opacity: 0;
  left: 2em;
}

.ani.fade-in-up {
  opacity: 0;
  top: 2em;
}

.ani.fade-in {
  opacity: 0;
}


